.WelcomeScreen {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background: url("../src/images/woods-background.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@font-face {
  font-family: 'Comme-Light';
  src: url('./images/comme.extralight.ttf');
  font-weight: normal;
  font-style: normal;
}

body, input, button {
  font-family: 'Comme-Light';
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}
.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #1D4355;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
  box-shadow: 0 0 6px #1D4355;
}
.google-btn:active {
  background: #1D4355;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}
.btn-text {
  float: right;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  background-color: #1D4355;
  width: 80%;
  height: 100%;
  text-align: center;
}

.WelcomeScreen a {
  color: #1D4355;
  font-weight: bolder;
}

.welcome_logo {
  margin: 10px 20px 10px 20px;
}

.welcome_message {
  margin: 0 10px 0 10px;
}